import React, {useState, useEffect} from 'react';
import {Input, Button, Form} from 'antd';
import {api} from '../../../utils/api';
import Layout from '../../../utils/layout/service';
import ModalCard from "../../../utils/modal-card";

const Forgot = ({isMenuShow, setIsMenuShow}) => {
  const [form] = Form.useForm();
  const [modal, setModal] = useState(null)

  const onFinish = async (value) => {
    const res = await api.recovery(value)
    setModal(res.data)
  }
  // {result: "error", message: "Invalid email"}
  return (
    <Layout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
      <div className='forgot'>
        <h1 className='title'>Forgot password?</h1>
        <Form name='forgot'
              form={form}
              onFinish={onFinish}>

          <Form.Item
            name='email'
            label='Email'
            rules={[
              {
                type: 'email',
                message: 'The input is not valid E-mail!',
              },
              {
                required: true,
                message: 'Please input your E-mail!',
              },
            ]}>
            <Input placeholder='Email'/>
          </Form.Item>
          <Form.Item className='border-none'>
            <Button className='blue' htmlType='submit'>Send</Button>
          </Form.Item>
        </Form>
      </div>
      {modal && <ModalCard reason='forgot' message={modal.message} setModal={setModal}/>}
    </Layout>
  );
};

export default Forgot;
