import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import {Switch, Route} from 'react-router-dom';
import Auth from './components/Auth';
import CaseChallenge from './components/CaseChallenge';
import Cases from './components/Cases';
import Main from './components/Main';
import Materials from './components/Materials';
import Buddies from './components/Buddies';
import PrivateAccount from './components/PrivateAccount';
import User from './components/User';
import {SET_COUNTRY, setCountry} from "./redux/reducers/country";
import {api} from "./utils/api";
import MainLayout from './utils/layout/main';
import ModalCard from "./utils/modal-card";
import NotFound from './utils/service';
import {resizeWidth, RESIZE_WINDOW} from './redux/reducers/window';

const language = ['English', 'Arabic', 'Turkish', 'Azeri', 'French', 'Russian', 'Kazakh', 'Portuguese', 'Afrikaans', 'Swahili', 'Spanish', 'Dutch', 'Italian', 'German']

const App = ({isAuth, resizeWidth, setCountry}) => {
  const [isMenuShow, setIsMenuShow] = useState(false)
  const [isModal, setIsModal] = useState(false)

  const onClick = e => {
    e.target.classList.length ?
      (e.target.classList[0].includes('user') && !isMenuShow ? setIsMenuShow(true) : setIsMenuShow(false))
      : setIsMenuShow(false)
  }

  const onResize = e => resizeWidth(RESIZE_WINDOW, e.target.innerWidth)
  useEffect(async () => {
    resizeWidth(RESIZE_WINDOW, document.documentElement.clientWidth)
    window.addEventListener('resize', onResize)

    const country = await api.country()
    setCountry(SET_COUNTRY, country.data)
    return () => {
      window.removeEventListener('resize', onResize)
    }
  }, [])

  return (
    <div className='app' onClick={onClick}>
      <Switch>

        <Route path='/' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <Main/>
          </MainLayout>}/>

        <Route path='/cases' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><Cases/></div>
          </MainLayout>}/>

        <Route path='/case-challenge' exact render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><CaseChallenge/></div>
          </MainLayout>}/>

        <Route path='/materials' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><Materials/></div>
          </MainLayout>}/>

        <Route path='/buddies' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><Buddies/></div>
          </MainLayout>}/>

        <Route path='/user' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><User/></div>
          </MainLayout>}/>

        {isAuth && <Route path='/account' render={() =>
          <MainLayout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
            <div className='container'><PrivateAccount language={language}/></div>
          </MainLayout>}/>}

        {!isAuth && <Auth isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}

        <Route render={() => <NotFound/>}/>
      </Switch>

      {!isAuth && isModal && <ModalCard reason='no register' setModal={setIsModal}/>}
    </div>
  );
}

const mstp = ({user}) => ({isAuth: user.isAuth})

export default connect(mstp, {resizeWidth, setCountry})(App)

const Container = ({childen}) => <div className='container'>{childen}</div>
