import axios from 'axios';
const baseUrl = 'http://127.0.0.1:8000/api/v1/'
export const api = {
  login: (data) => axios.post(baseUrl + 'user/auth', data),
  createUser: (data) => axios.post(baseUrl + 'user/create', data),
  profileUpdate: (data) => axios.post(baseUrl + 'user/profile/update', data),
  recovery: (data) => axios.post(baseUrl + 'user/password/recovery', data),
  updatePassword: (data) => axios.post(baseUrl + 'user/password/change', data),
  confirmPassword: (data) => axios.get(baseUrl + 'user/password/confirm'),

  partners: (data) => axios.get(baseUrl + `partners/get${data ? '?'+ data : ''}`),
  userProfile: (id) => axios.get(baseUrl + `partners/get/${id}/`),
  country: () => axios.get(baseUrl + 'user/country'),
  language: () => axios.get(baseUrl + 'user/language'),
  tags: () => axios.get(baseUrl + 'user/tags'),
}
