const initState = []

export const SET_COUNTRY = 'SET_COUNTRY'

export const country = (state = initState, action) => {
  switch (action.type) {
    case SET_COUNTRY: {
      return [...action.country]
    }
    default: {
      return state
    }
  }
}

export const setCountry = (type, country) => ({type, country})
