import React from 'react';
import {Route, Switch} from 'react-router-dom';
import NotFound from "../../utils/service";
import Forgot from './Forgot';
import Login from './Login';
import MailNotConfirmed from './MailNotConfirmed';
import PasswordRecovery from './PasswordRecovery';
import Register from './Register';

const Auth = ({isMenuShow, setIsMenuShow, language}) => {
  return (
    <Switch>
      <Route path='/login' render={() => <Login isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/forgot' render={() => <Forgot isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/mail-not-confirmed' render={() => <MailNotConfirmed isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/password-recovery' render={() => <PasswordRecovery isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}/>}/>
      <Route path='/register' render={() => <Register isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow} language={language}/>}/>
      <Route render={() => <NotFound/>}/>
    </Switch>
  );
};

export default Auth;
