import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import cn from 'classnames';
import {setCurrentPage} from '../../../redux/reducers/page';
import Layout from '../../../utils/layout/service';
import {FirstStep} from './first-step';
import {SecondStep} from './second-step';
import {ThreeStep} from './three-step';

const Register = ({page, setCurrentPage, isMenuShow, language, setIsMenuShow, width, country, years}) => {
  const [step, setStep] = useState(1)
  const [finishFirstStep, setFinishFirstStep] = useState(null)
  const [finishSecondStep, setFinishSecondStep] = useState(null)
  const [dataCountry, setDataCountry] = useState(null)
  const [flagsPhoneCode, setFlagsPhoneCode] = useState(null)

  useEffect(() => {
    setDataCountry([...country.map(({id, name}) => ({id, name}))])
    setFlagsPhoneCode([...country.map(({id, alias, calling_codes}) => ({id, alias, calling_codes})).filter(i => i.calling_codes && i)])
  }, [country])

  useEffect(() => {

  }, [finishFirstStep, finishSecondStep])
  return (
    <Layout isMenuShow={isMenuShow} setIsMenuShow={setIsMenuShow}>
      <div className='register'>
        {step === 1
          ? <>
            <h1 className='title'>{page}</h1>
            <div className='steps'>
              {width > 680 && <div className={cn('step', {'current-step': step === 2})}/>}
            </div>
            <FirstStep dataCountry={dataCountry} flagsPhoneCode={flagsPhoneCode} setCurrentPage={setCurrentPage} setStep={setStep} setFinish={setFinishFirstStep}/>
          </>
          : step === 2
            ? <SecondStep step={step} setStep={setStep} setFinish={setFinishSecondStep}
                          width={width} language={language} years={years}
                          initials={finishFirstStep.firstName[0] + finishFirstStep.lastName[0]}
                          name={finishFirstStep.firstName + ' ' + finishFirstStep.lastName}/>
            : <ThreeStep/>
        }
      </div>
    </Layout>
  )
};

export default connect(({page, windowSize, country, years}) => ({...page, width: windowSize.width, country, years}), {setCurrentPage})(Register);
